import {Component, ViewChild, OnInit} from '@angular/core';
import {LinkedInService} from 'angular-linkedin-sdk';
import {FacebookService} from '../services/FacebookProvider/facebook.service';
import {SlasherDataService} from '../services/slasher-data.service';
import { MdcSnackbar } from '@angular-mdc/web';
import {Router} from '@angular/router';
import { CustomValidators } from 'ng4-validators';

import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';

@Component({
    selector: 'slashers-inscription',
    templateUrl: './inscription.component.html',
    styleUrls: ['./inscription.component.scss']
})
export class InscriptionComponent implements OnInit {
    public account: { email: string, password: string, confirmation: string } = {email: null, password: null, confirmation: null};
    form: FormGroup;

    constructor(
      private fb: FacebookService,
      private linkedInService: LinkedInService,
      public slasherDataService: SlasherDataService,
      public mdcSnackbar: MdcSnackbar,
      private router: Router,
      private formBuilder: FormBuilder
      ) {}


    ngOnInit() {
      let password = new FormControl('', [Validators.required, Validators.minLength(7)]);
      let confirmation = new FormControl('', [Validators.required, CustomValidators.equalTo(password)]);

      this.form = this.formBuilder.group({
        email: ['', [Validators.required, Validators.email]],
        password: password,
        confirmation: confirmation
      });
    }


    registerWithFacebook(): void {
        this.fb.login().then((response) => {
            console.log(response);
            this.fb.getData().then((data) => {
                console.log(data);
            }).catch((error: any) => console.error(error));
        }).catch((error: any) => console.error(error));
    }

    registerWithLinkedIn() {
        this.linkedInService.login().subscribe({
            next: (state) => {
                console.log(state);
            },
            complete: () => {
                // Completed
            }
        });
    }


    onRegister() {
      this.slasherDataService.signup(this.account.email, this.account.password)
      .then(
        (data) => {
          if (data.statusCode == 409) {
            this.mdcSnackbar.show('Email dj utilis', 'OK'
              // {
              //   duration: 5000
              // }
            );
          } else {
            this.router.navigate(['/presentation']);
          }
        }, (error) => {
          console.log(error);
        }
      )
    }
}



