import {Component, ViewChild, Output, EventEmitter, OnInit} from '@angular/core';
import {Subject} from 'rxjs/Subject';
import {MockSearchDomainService} from '../../services/mock-search-domain.service';
import {GlobalsService} from '../../services/globals/globals.service';
import * as _ from "lodash";

@Component({
    selector: 'slashers-search-role',
    templateUrl: './search-role.component.html',
    styleUrls: ['./search-role.component.scss']
})
export class SearchRoleComponent implements OnInit {

  @Output() addSearchRole: EventEmitter<any> = new EventEmitter();

  roleSelected: string;
  domainTerm: string;
  domainTerm$ = new Subject<string>();
  categorySelected: string;
  panelSearchOpened = true;

  results: any[];

  roles = [
    'Non prcis',
    'Une mission freelance',
    'Un engagement bnvole',
    'Un stage',
    'Un nouveau job',
    'Un poste d\'associ.e',
    'Une mission freelance',
    'Un engagement bnvole',
    'Un stage',
    'Un nouveau job',
    'Un poste d\'associ.e',
    'Une mission freelance',
    'Un engagement bnvole',
    'Un stage',
    'Un nouveau job',
    'Un poste d\'associ.e'
  ];

  ecosystemKeys: string[];

  @ViewChild('searchDomain') searchDomain: any;

  constructor(public globals: GlobalsService, public searchService: MockSearchDomainService){
    this.searchService.search(this.domainTerm$)
      .subscribe(results => {
        if (!results) {
          this.results = null;
          return;
        }
        let indexDomainTerm = _.findIndex(results, (x: any) => {
          return x.name == this.domainTerm;
        });
        if (indexDomainTerm === -1) {
          let term = {name:this.domainTerm, nbShots: 0};
          results = [term, ...results];
        } else {
          results = [
            results[indexDomainTerm],
            ..._.slice(results, 0, indexDomainTerm),
            ..._.slice(results, indexDomainTerm + 1)
          ]
        }
        this.results = results;
      });
  }

  ngOnInit() {
    this.searchDomain.inputText.nativeElement.onfocus = () => {
      // wait for the clickOutside propagation
      setTimeout(()=> {
        this.panelSearchOpened = true;
      })
    };

    this.ecosystemKeys = Object.keys(this.globals.EcosystemCategory);
  }

  clickOnDomain(event: any, domain: any) {
    event.stopPropagation();

    this.domainTerm = domain.name;
    this.results = [domain];
  }

  clickOnCategory(category: string) {
    this.categorySelected = category
    this.panelSearchOpened = false;
  }

  addRole() {
    this.addSearchRole.emit({
      role: this.roleSelected,
      domain: this.domainTerm,
      category: this.categorySelected
    });
  }


}



