import {Component, EventEmitter, Input, Output} from '@angular/core';
import {Router} from '@angular/router';

@Component({
    selector: 'slashers-profile-toolbar',
    templateUrl: './profile-toolbar.component.html',
    styleUrls: ['./profile-toolbar.component.scss']
})
export class ProfileToolbarComponent {
    @Input() title: string;
    @Input() hasSearch: boolean = false;
    @Input() hasMore: boolean = false;
    @Input() hasValidate: boolean = false;
    @Input() hasPrev: boolean = false;
    @Input() previous: string;
    @Input() progress: string;
    @Input() prevEnabled: boolean = true;
    @Output() goPrevious: EventEmitter<any> = new EventEmitter();
    @Output() validate: EventEmitter<any> = new EventEmitter();

    constructor(private router: Router){}

    goPrev() {
      if (this.prevEnabled) {
        if (this.previous) {
          this.router.navigate(['/' + this.previous]);
        } else {
          this.goPrevious.emit();
        }
      }
    }

}



