import {Component, OnInit, ViewChild} from '@angular/core';
import {SlasherDataService} from '../../services/slasher-data.service';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

@Component({
    selector: 'slashers-story-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.scss'],
    animations: [
      trigger('checkClicked', [
        state('true', style({ transform: 'scale(0)', marginLeft:'-44px', marginTop: '-44px' })), // 44 due to fab button
        transition('0 => 1', animate('200ms')),
      ])
    ]
})
export class StoryhomeComponent implements OnInit {

  // imageSrc = './assets/img/slasher.jpg';
  checkClicked = false;
  slasherData: any;
  imageSrc: string;
  cropperPosition: any;

  @ViewChild('fullCroopedImage') fullCroopedImage: any;

  constructor(public slasherDataService: SlasherDataService){} 

  ngOnInit() {
    this.slasherDataService.slasherData$.subscribe((data) => {
      this.slasherData = data;
      if (this.slasherData.imageSrc) {
        this.imageSrc = this.slasherData.imageSrc;
        this.cropperPosition = this.slasherData.cropperPosition;
      } else {
        this.imageSrc = './assets/img/profile-img.jpg';
        this.cropperPosition = {
          tlPosition: {x:0, y:0},
          brPosition: {x:1, y:1}
        };
      }
    });

  }


  btnclick()
  {

    if(this.checkClicked)
        {
          this.checkClicked = false;
          
        }
    else
      {
         this.checkClicked = true;
      }

  }

}



