import {Injectable} from '@angular/core';
import {FacebookService as FBService, InitParams, LoginOptions, LoginResponse} from 'ngx-facebook';

@Injectable()
export class FacebookService {
    loginOptions: LoginOptions = {
        enable_profile_selector: true,
        return_scopes: true,
        scope: 'public_profile,email'
    };

    constructor(private fb: FBService) {
        const initParams: InitParams = {
            appId: '1408787492562997',
            xfbml: true,
            version: 'v2.11'
        };
        fb.init(initParams);
    }

    login() {
        return new Promise((resolve, reject) => {
            this.fb.login(this.loginOptions).then((response: LoginResponse) => {
                resolve(response);
            }).catch(reject);
        });
    }

    getData() {
        return new Promise((resolve, reject) => {
            this.fb.api('/me?fields=id,email,first_name,last_name,cover,picture').then(response => {
                resolve(response);
            }).catch(reject);
        });
    }
}



