import {Component, Input, Output, EventEmitter} from '@angular/core';

@Component({
    selector: 'slashers-profile-identity',
    templateUrl: './profile-identity.component.html',
    styleUrls: ['./profile-identity.component.scss']
})
export class ProfileIdentityComponent {

    @Input() imageSrc: string;
    @Input() avatarSrc: string;
    @Input() editable: boolean = false;
    @Input() blur: boolean = false;
    @Input() informations: {name: string, slashes: string, location:string};

    @Output() updateAvatar: EventEmitter<any> = new EventEmitter();

    constructor() {}

}



