import {Component, OnInit, ElementRef} from '@angular/core';
import {SlasherDataService} from '../services/slasher-data.service';
import * as _ from "lodash";
import {Router} from '@angular/router';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

@Component({
    selector: 'slashers-welcome',
    templateUrl: './welcome.component.html',
    styleUrls: ['./welcome.component.scss'],
    animations: [
      trigger('editSlashes', [
        // state('true' , style({ opacity: 1, transform: 'scale(1.0)' })),
        // state('false', style({ opacity: 0, transform: 'scale(0.0)'  })),
        state('true' , style({ height: '*' })),
        state('false', style({ height: '0' })),
        transition('1 <=> 0', animate('300ms')),
        // transition('0 => 1', animate('900ms'))
      ]),
      trigger('slideProfileExtract', [
        state('slide', style({transform: 'translate(-100%, 0)'})),
        state('place', style({transform: 'translate(0, 0)'})),
        transition('place => slide', animate('500ms')),
      ])
    ]
})
export class WelcomeComponent implements OnInit {

    editSlashes = false;

    avatarSrc = "./assets/img/slasher.jpg";

    slasherData: any;
    slashes: string[];

    profileExtracts: ElementRef[];
    indexProfileExtractsData: number;
    slideProfileExtractState: string;

    form: FormGroup;

    otherProfileExtracts = [
      {imageSrc: './assets/img/slasher.jpg', title: 'Fabricio Lavandier', subtitle: 'Producteur / Musicien / Scnariste'},
      {imageSrc: './assets/img/slasher.jpg', title: 'Jean Dupont', subtitle: 'Dessinateur / Chanteur'},
      {imageSrc: './assets/img/slasher.jpg', title: 'Patrice Martin', subtitle: 'Magicien / Ralisateur'},
      {imageSrc: './assets/img/slasher.jpg', title: 'Sbastien Durand', subtitle: 'Design'},
      {imageSrc: './assets/img/slasher.jpg', title: 'Bernard Pascal', subtitle: 'Dveloppeur'},
      // repeat first (do it better)
      {imageSrc: './assets/img/slasher.jpg', title: 'Fabricio Lavandier', subtitle: 'Producteur / Musicien / Scnariste'}
    ];

    constructor(
      public slasherDataService: SlasherDataService,
      private router: Router,
      private formBuilder: FormBuilder
      ){}

    ngOnInit() {
      this.slasherDataService.slasherData$.subscribe((data) => {
        this.slasherData = data;

        let emptyArray = _.fill(new Array(5), '');
        this.slashes = _.map(emptyArray, (x, i) => {
          if (this.slasherData.slashes.length > i) {
            return this.slasherData.slashes[i];
          }
        });
        console.log(this.slashes);

      });

      this.form = this.formBuilder.group({});

      this.initializeAnimationProfileExtract();

    }

    initializeAnimationProfileExtract() {
      this.indexProfileExtractsData = 0;
      this.slideProfileExtractState = 'place';
      setTimeout(()=> {
        this.animateProfileExtracts();
      },3000);
    }

    animateProfileExtracts() {
      this.slideProfileExtractState = 'slide';
      setTimeout(()=> {
        this.animateProfileExtracts();
      },3000);
    }

    slideProfileExtractAnimationDone(event) {
      if (event.toState === 'slide') {
        setTimeout(()=> {
          this.slideProfileExtractState = 'place';
          this.indexProfileExtractsData = (this.indexProfileExtractsData + 1) % (this.otherProfileExtracts.length - 1);
        })

      }
    }

    slasherChoiceFocus() {
      this.editSlashes = true;
    }

    getSlasherProfileExtractName() {
      return this.slasherData.firstName + ' ' + this.slasherData.lastName;
    }

    getSlasherProfileExtractSlashes() {
      return _.join(_.compact(this.slashes), ' / ');
    }

    goPrev() {
      if (!this.slasherDataService.slasherData.imageSrc) {
        this.router.navigate(['/take-picture']);
      } else {
        this.router.navigate(['/take-avatar']);
      }
    }

    goNext() {
      this.slasherData.slashes = _.compact(this.slashes);
      this.slasherDataService.update();
      this.router.navigate(['/intro-1-story']);
    }
}



