import { Component, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'rio-counter',
  templateUrl: './counter.component.html'
})
export class CounterComponent {
  @Input() count = 0;
  

  increment() {
    
    this.count++;
    
  }
}


