import {Component, OnInit} from '@angular/core';
import {SlasherDataService} from '../../services/slasher-data.service';
import {Router} from '@angular/router';

@Component({
    selector: 'slashers-profile-take-avatar',
    templateUrl: './profile-take-avatar.component.html',
    styleUrls: ['./profile-take-avatar.component.scss'],
})
export class ProfileTakeAvatarComponent implements OnInit {

  avatarSrc: string;
  slasherData: any;

  constructor (public slasherDataService: SlasherDataService, private router: Router){}

  ngOnInit() {
    this.slasherDataService.slasherData$.subscribe((data) => {
      this.slasherData = data;
    });
  }

  onSetImageData(event) {
    this.slasherDataService.slasherData.avatarSrc = event.cropperSrc;
    this.slasherDataService.slasherData.cropperPosition = event.cropperPosition;
  }

  validate() {
    this.slasherDataService.update()
    .then(
      (data) => {
        this.router.navigate(['/profile-update']);
      }, (error) => {
        console.log(error);
      }
    )
  }

  // goNext() {
  //   this.slasherDataService.update()
  //   .then(
  //     (data) => {
  //       this.router.navigate(['/welcome']);
  //     }, (error) => {
  //       console.log(error);
  //     }
  //   )
  // }


}



