import {Component, Input, OnInit} from '@angular/core';

@Component({
    selector: 'slashers-ecosystem-tag',
    templateUrl: './ecosystem-tag.component.html',
    styleUrls: ['./ecosystem-tag.component.scss']
})
export class EcosystemTagComponent implements OnInit {
    @Input() color: string;
    @Input() name: string;
    @Input() imageSrc: string;
    @Input() badge: number;
    @Input() fill: boolean;

    textColor: string;
    backgroundColor: string;
    borderColor: string;


    constructor() {}

    ngOnInit() {
      if (this.fill) {
        this.textColor = '#fff';
        this.backgroundColor = this.color;
        this.borderColor = 'transparent';
      } else {
        this.textColor = this.color;
        this.backgroundColor = 'transparent';
        this.borderColor = this.color;
      }
    }
}



