    $('.list2').css('top', cords.y+18).css('left', cords.x).show();
    var autoListH = this.getAutoListHeight('keywordcat');
        $(".list2").css('height',autoListH).show();
    this.keywordCatShow = true;
    this.selectedKeyword = selectedKeyword;
