  floatSuggestion(e: any) {
    let fromTop = $('.top-div').height();
    let currentPos = e.screenY + 'px';
    // reset the suggestion list Y pos
    $('.autolist').css('top', currentPos);
    // keyCodes may different on OS and versions !
    if (e.keyCode == 51 || e.keyCode == 35 || e.keyCode == 229) {
      $('#titre').css('opacity', '0')
      $('#description').addClass('float-up');
      this.enableAutoSearch(e);
    }
  }

  updateKeywordListByStr(inputStr) {
    this.matchKeywords = [];
    let i;
    inputStr = inputStr.trim();
    var re = new RegExp(inputStr, 'g');
    for (i = 0; i < this.desckeywords.length; i++) {
      let currKeyword = this.desckeywords[i].name;
      // matchKeywords
      if (currKeyword.match(re)) {
        this.matchKeywords.push(this.desckeywords[i]);
      }
    }
  }
  checkStr(inputtxt) {
    var letters = /^[0-9a-zA-Z]+$/;
    if (inputtxt.match(letters)) {
      return true;
    }
    return false;

  }
  getAutoListPos() {
    var atw = $('.autotext').width();
    let autoTxtWidth: number = atw;
    let caretXPos: number = parseInt(this.savedCords.x);
    let containerWidth: number = $('#description').width();
    let posCss = {'pos':'left', 'x':caretXPos};
    // containerWidth < (autoTxtWidth + caretXPos + 50)
    let compareWidth: number = containerWidth - caretXPos;
    /*
    if (containerWidth < autoTxtWidth+20) {
        posCss = {'pos':'left', 'x':15};
    } else if (containerWidth > autoTxtWidth+20) {
        let testw = caretXPos + autoTxtWidth;
        if (testw > containerWidth) {
            let rightpos = containerWidth - testw;
            rightpos = Math.abs(rightpos);
            posCss = {'pos':'right', 'x':rightpos};
        } else {
            posCss = {'pos':'left', 'x':caretXPos};
        }
      //  posCss = {'pos':'right', 'x':rightPos};
    }*/ // 415
    if (containerWidth < 415) {
        posCss = {'pos':'left', 'x':15};
    } else {
        let testw = caretXPos + autoTxtWidth;
        if (testw < containerWidth) {
            posCss = {'pos':'left', 'x':caretXPos};
        } else {
            let rightpos = screen.width - caretXPos;
            rightpos = Math.abs(rightpos);
            posCss = {'pos':'right', 'x':rightpos};
        }
    }
    /*
    if (containerWidth < 400 && (containerWidth <= autoTxtWidth+20)) {
      posCss = {'pos':'left', 'x':15};
    } else if (compareWidth <= (autoTxtWidth+10)) {
      posCss = {'pos':'right', 'x':compareWidth};
  }*/


    return posCss;
  }
  getAutoListHeight(type) {
      var screenHeight = window.screen.availHeight;
    var pageHeight = window.innerHeight;
    if (type == 'keyword') {
        var autoListTopPos = $(".autotext").position().top;
        var autoListH = $(".autotext").height();
    } else if (type == 'keywordcat') {
        var autoListTopPos = $(".list2").position().top;
        var autoListH = $(".list2").height();
    }

    var bottomH = $('#bottom-bar').height();
    // var autoListRemainH = pageHeight - autoListTopPos - bottomH - 5;
    var autoListRemainH = pageHeight - autoListTopPos;
    if (autoListRemainH > autoListH) {
        autoListRemainH = autoListH;//+'px';
    } else {
        autoListRemainH = autoListRemainH;//+'px';
    }

    return autoListRemainH;

  }
  getCharacterPrecedingCaret(containerEl) {
    var precedingChar = "", sel, range, precedingRange;
    if (window.getSelection) {
        sel = window.getSelection();
        if (sel.rangeCount > 0) {
            range = sel.getRangeAt(0).cloneRange();
            range.collapse(true);
            range.setStart(containerEl, 0);
            precedingChar = range.toString().slice(-1);
        }
    } else if ( (sel = document['selection']) && sel.type != "Control") {
        range = sel.createRange();
        precedingRange = range.duplicate();
        precedingRange.moveToElementText(containerEl);
        precedingRange.setEndPoint("EndToStart", range);
        precedingChar = precedingRange.text.slice(-1);
    }
    return precedingChar;
}
  pasteKeywordHtml(keyhtml) {
    var editor = document.getElementById("description");
    var range = this.saveRange;
    var sel = this.savedsel;
    range.deleteContents();
    $(".keymarker").remove();
    // Range.createContextualFragment() would be useful here but is
    // non-standard and not supported in all browsers (IE9, for one)
    var el = document.createElement("div");
    el.innerHTML = keyhtml;

    var frag = document.createDocumentFragment(), node, lastNode;
    while ((node = el.firstChild)) {
      lastNode = frag.appendChild(node);
    }
    range.insertNode(frag);

    // Preserve the selection
    if (lastNode) {
      range = range.cloneRange();
      range.setStartAfter(lastNode);
      range.collapse(true);
      sel.removeAllRanges();
      sel.addRange(range);
    }
    $('.autotext').hide();
    $('.list2').hide();
    $('#bottom-bar').show();
    this.keywordShow = false;
    this.keywordCatShow = false;
    editor.normalize();
    return false;

  }
  getSelectionCoords() {
    var sel = document['selection'], range, rect;
    this.savedsel = sel;
    var x = 0, y = 0;
    if (sel) {
      if (sel.type != "Control") {
        range = sel.createRange();
        range.collapse(true);
        this.saveRange = range;
        x = range.boundingLeft;
        y = range.boundingTop;
      }
    } else if (window.getSelection) {
      sel = window.getSelection();
      this.savedsel = sel;
      if (sel.rangeCount) {
        range = sel.getRangeAt(0).cloneRange();
        this.saveRange = range;
        if (range.getClientRects) {
          range.collapse(true);
          if (range.getClientRects().length > 0) {
            rect = range.getClientRects()[0];
            x = rect.left;
            y = rect.top;
          }
        }
        // Fall back to inserting a temporary element
        if (x == 0 && y == 0) {
          var span = document.createElement("span");
          if (span.getClientRects) {
            // Ensure span has dimensions and position by
            // adding a zero-width space character
            span.appendChild(document.createTextNode("\u200b"));
            range.insertNode(span);
            rect = span.getClientRects()[0];
            x = rect.left;
            y = rect.top;
            var spanParent = span.parentNode;
            spanParent.removeChild(span);

            // Glue any broken text nodes back together
            spanParent.normalize();
          }
        }
      }
    }
    return { x: x, y: y };
  }
  selectElementText(el, win){
    win = win || window;
    var doc = win.document, sel, range;
    if (win.getSelection && doc.createRange) {
        range = doc.createRange();
        range.selectNodeContents(el);
        range.collapse(false);
        sel = win.getSelection();
        sel.removeAllRanges();
        sel.addRange(range);
    }
    else
        if (doc.body.createTextRange) {
            range = doc.body.createTextRange();
            range.moveToElementText(el);
            range.select();
        }
}
