import {Component, OnInit, Input, ViewChild, NgZone, Output, EventEmitter} from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { } from 'googlemaps';
import { AgmCoreModule, MapsAPILoader } from '@agm/core';

@Component({
    selector: 'slashers-input-location',
    templateUrl: './input-location.component.html',
    styleUrls: ['./input-location.component.scss']
})
export class InputLocationComponent implements OnInit {

  @Input() form: FormGroup;
  @Input() data: any;
  @Output() placeChanged: EventEmitter<any> = new EventEmitter();

  @ViewChild('search') search: any;

  autocomplete: any;

  constructor(
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone
  ) {}

  ngOnInit() {
    this.mapsAPILoader.load().then(() => {
      this.autocomplete = new google.maps.places.Autocomplete(this.search.inputText.nativeElement, {
        types: ['(cities)'],
        componentRestrictions: { country: 'FR' }
      });
      this.autocomplete.addListener("place_changed", () => {
        this.searchPlace();
      });
    });

    this.form.addControl('location', new FormControl('', [Validators.required]));

  }

  searchPlace() {
    this.ngZone.run(() => {
      //get the place result
      let place: google.maps.places.PlaceResult = this.autocomplete.getPlace();

      // this.slasherData.location = place.formatted_address;
      this.placeChanged.emit(place);

      // console.log(place);

      //verify result
      // if (place.geometry === undefined || place.geometry === null) {
      //   return;
      // }
    });
  }
  //
  // blur() {
  //   console.log('blur');
  //   // this.searchPlace();
  // }


}



