import {Injectable} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/toPromise';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class SlasherDataService {

    slasherData = {
      imageSrc: '',
      cropperPosition: {},
      avatarSrc: '',
      firstName: '',
      lastName: '',
      age: undefined,
      location: '',
      slashes: []
      // biography: '',
      // facebookLink: '',
      // linkedinLink: '',
      // twitterLink: '',
      // otherLinks: []
    }

    slasherData$ = new BehaviorSubject<any>(this.slasherData);

    constructor(private http: HttpClient) {
      if (localStorage.getItem('slasherId')) {
        this.getSlasher();
        // this.update();
      }
    }

    getSlasher(): Promise<any> {
      let body = {
        id: localStorage.getItem('slasherId')
      };
      return this.http
      .post('https://aurelcamp.fr/api/user/get.php', body).toPromise()
      .then( (data: any) => {

        data.result.slashes = data.result.slashes && JSON.parse(data.result.slashes);
        data.result.cropperPosition = data.result.cropperPosition && JSON.parse(data.result.cropperPosition);

        this.slasherData = data.result;
        this.slasherData$.next(data.result);
      })
    }

    signup(email: string, password: string): Promise<any> {
      let body = {
        email: email,
        password: password
      };
      return this.http
      .post('https://aurelcamp.fr/api/user/create.php', body).toPromise()
      .then( (data: any) => {
        if (data.statusCode == '201') {
          localStorage.setItem('slasherId',data.result.id);
        }
        return data;
      });
      // .then((data)=> {
      //   console.log(data);
      // })
      // See below - subscribe() is still necessary when using post().
      // .subscribe((message) => {
      //   console.log(message);
      // }, (error)=> {
      //   console.log(error);
      // });
    }

    connection(email: string, password: string): Promise<any> {
      let body = {
        email: email,
        password: password
      };
      return this.http
      .post('https://aurelcamp.fr/api/user/connection.php', body).toPromise()
      .then( (data: any) => {
        if (data.statusCode == '200') {
          localStorage.setItem('slasherId',data.result.id);
        }
        return data;
      });
    }

    update(): Promise<any> {
      let body = {
        id: localStorage.getItem('slasherId'),
        ...this.slasherData
      }

      return this.http
      .post('https://aurelcamp.fr/api/user/update.php', body).toPromise();

      // let body = {
      //   id: localStorage.getItem('slasherId'),
      //   slashes: ['Producteur', 'Designeur'];
      // }
      // return this.http
      // .post('http://les-combien.com/slashers/api/user/update.php', body)
      // .subscribe((message) => {
      //   console.log(message);
      // }, (error)=> {
      //   console.log(error);
      // });
    }

}



