import {Component, OnInit} from '@angular/core';
import {LinkedInService} from 'angular-linkedin-sdk';
import {FacebookService} from '../services/FacebookProvider/facebook.service';
import {SlasherDataService} from '../services/slasher-data.service';
import { MdcSnackbar } from '@angular-mdc/web';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';

@Component({
    selector: 'slashers-connexion',
    templateUrl: './connexion.component.html',
    styleUrls: ['./connexion.component.scss']
})
export class ConnexionComponent implements OnInit {
    public account: { email: string, password: string } = {email: null, password: null};
    form: FormGroup;

    constructor(
      private fb: FacebookService,
      private linkedInService: LinkedInService,
      public slasherDataService: SlasherDataService,
      private mdcSnackbar: MdcSnackbar,
      private formBuilder: FormBuilder
      ) {}

    ngOnInit() {

      this.form = this.formBuilder.group({
        email: ['', [Validators.required]],
        password: ['', [Validators.required]]
      });
    }

    loginWithFacebook(): void {
        this.fb.login().then((response) => {
            console.log(response);
            this.fb.getData().then((data) => {
                console.log(data);
            }).catch((error: any) => console.error(error));
        }).catch((error: any) => console.error(error));
    }

    loginWithLinkedIn() {
        this.linkedInService.login().subscribe({
            next: (state) => {
                console.log(state);
            },
            complete: () => {
                // Completed
            }
        });
    }


    onLogin() {
      this.slasherDataService.connection(this.account.email, this.account.password)
      .then(
        (data) => {
          console.log(data);
          if (data.statusCode == 409) {
            this.mdcSnackbar.show('Email ou mdp incorrect', 'OK');
          }
        }, (error) => {
          console.log(error);
        }
      );
        // console.log(this.account);
    }
}



