import {Component, OnInit} from '@angular/core';
import {SlasherDataService} from '../../services/slasher-data.service';
import * as _ from "lodash";
import { CustomValidators } from 'ng4-validators';
import {Router} from '@angular/router';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl
} from '@angular/forms';

@Component({
    selector: 'slashers-profile-update',
    templateUrl: './profile-update.component.html',
    styleUrls: ['./profile-update.component.scss'],
})
export class ProfileUpdateComponent implements OnInit {

  slasherData: any;
  slashes: string[];
  form: FormGroup;

  constructor (public slasherDataService: SlasherDataService, private formBuilder: FormBuilder, private router: Router){}

  ngOnInit() {
    this.slasherDataService.slasherData$.subscribe((data) => {
      this.slasherData = data;

      let emptyArray = _.fill(new Array(5), '');
      this.slashes = _.map(emptyArray, (x, i) => {
        if (this.slasherData.slashes.length > i) {
          return this.slasherData.slashes[i];
        }
      });

    });

    this.form = this.formBuilder.group({
      firstName: ['', [Validators.required]],
      lastName: ['', [Validators.required]],
      age: ['', [Validators.required, CustomValidators.min(16)]],
      biography: [''],
      // location: ['', [Validators.required]]
    });

    // this.form = this.formBuilder.group({});

  }

  placeChanged(place: any) {
    if (place && place.geometry !== undefined && place.geometry !== null) {
      this.slasherData.location = place.formatted_address;
      let locationGPS = {lat: place.geometry.location.lat(), long: place.geometry.location.lng()};
      console.log(locationGPS);
    }
  }

  updateAvatar() {
    this.router.navigate(['/profile-take-picture']);
  }

  validate() {
    if (this.form.valid) {
      console.log('ok');
    } else {
      console.log('pas ok');
    }
    this.router.navigate(['/profile']);
  }


}



