import {Component, Input, OnInit, ViewChild, ChangeDetectorRef, Output, EventEmitter} from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'slashers-slashes-choice',
    templateUrl: './slashes-choice.component.html',
    styleUrls: ['./slashes-choice.component.scss']
})
export class SlashesChoiceComponent implements OnInit {
    @Input() slashes: string[];
    @Input() form: FormGroup;

    @Output() focus: EventEmitter<any> = new EventEmitter<any>();


    @ViewChild('slashe0') slashe0;
    @ViewChild('slashe1') slashe1;
    @ViewChild('slashe2') slashe2;
    @ViewChild('slashe3') slashe3;
    @ViewChild('slashe4') slashe4;

    constructor(private cd: ChangeDetectorRef){}

    ngOnInit() {
      this.form.addControl('slashe0', new FormControl('', [Validators.required]));
      this.form.addControl('slashe1', new FormControl(''));
      this.form.addControl('slashe2', new FormControl(''));
      this.form.addControl('slashe3', new FormControl(''));
      this.form.addControl('slashe4', new FormControl(''));
      this.cd.detectChanges(); // because ExpressionChangedAfterItHasBeenCheckedError (do it better)

      setTimeout(()=> {
        let onFocus = () => {
          this.focus.emit();
        };
        this.slashe0.inputText.nativeElement.onfocus = onFocus;
        this.slashe1.inputText.nativeElement.onfocus = onFocus;
        this.slashe2.inputText.nativeElement.onfocus = onFocus;
        this.slashe3.inputText.nativeElement.onfocus = onFocus;
        this.slashe4.inputText.nativeElement.onfocus = onFocus;
      });
    }
}



