import {Component, Input} from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

@Component({
    selector: 'slashers-profile-ecosystem-container',
    templateUrl: './profile-ecosystem-container.component.html',
    styleUrls: ['./profile-ecosystem-container.component.scss'],
    animations: [
      trigger('shrinkOut', [
        state('opened', style({height: '*'})),
        transition('* => void', [
          style({height: '*'}),
          animate('100ms ease-in-out', style({height: 0}))
        ]),
        transition('void => *', [
          style({height: '0'}),
          animate('100ms ease-in-out', style({height: '*'}))
        ])
      ]),
      trigger('rotateCategoryArrow', [
        state('opened', style({transform: 'rotate(-180deg)'})),
        state('closed', style({transform: 'rotate(0deg)'})),
        transition('opened <=> closed', animate('100ms ease-in-out')),
      ])
    ]
})
export class ProfileEcosystemContainerComponent {
    @Input() title: string;
    @Input() ecosystem: any[]; // TODO interface
    @Input() editable: boolean = false;

    toggleEcosystemText = 'Tout ouvrir';

    constructor() {}

    toggleCategory(category: any) {
      if (category.nbGroupedElements === 0) return;
      category.state = (category.state === 'opened') ? 'closed' : 'opened';
      this.displayToggleEcosystemText();
    }

    displayToggleEcosystemText() {
      this.toggleEcosystemText = this.checkCategoriesAllClosed() ? 'Tout ouvrir' : 'Tout fermer';
    }

    checkCategoriesAllClosed(): boolean {
      let allClosed = true;
      for (let i=0; i<this.ecosystem.length; i++) {
        let category = this.ecosystem[i];
        if (category.state === 'opened') {
          allClosed = false;
          break;
        }
      }
      return allClosed;
    }

    toggleEcosystem() {
      let newState = this.checkCategoriesAllClosed() ? 'opened' : 'closed';
      for (let i=0; i<this.ecosystem.length; i++) {
        let category = this.ecosystem[i];
        category.state = newState;
        if (category.nbGroupedElements === 0) {
          category.state = 'closed';
        }
      }
      this.displayToggleEcosystemText();
    }

}



