import {Component, OnInit} from '@angular/core';
import {SlasherDataService} from '../services/slasher-data.service';
import * as _ from "lodash";

@Component({
    selector: 'slashers-welcome1',
    templateUrl: './welcome1.component.html',
    styleUrls: ['./welcome1.component.scss']
})
export class Welcome1Component implements OnInit {
    inputs = [{oldVal: null, newVal: null}];

    slasherData: any;
    slashes: string[];
    profileExtractSlashes: string;
    profileExtractName: string;

    constructor(public slasherDataService: SlasherDataService) {}

    ngOnInit() {
      // this.avatarSrc = this.slasherDataService.slasherData.avatarSrc;Vinaurel2594!
      this.slasherDataService.slasherData$.subscribe((data) => {
        this.slasherData = data;

        this.profileExtractSlashes = _.join(data.slashes, ' / ');
        this.profileExtractName = data.firstName + ' ' + data.lastName;

        let emptyArray = _.fill(new Array(5), '');
        this.slashes = _.map(emptyArray, (x, i) => {
          if (this.slasherData.slashes.length > i) {
            return this.slasherData.slashes[i];
          }
        });

      });
    }

    goNext() {
      this.slasherData.slashes = _.compact(this.slashes);
      this.slasherDataService.update();
    }

    // onChange(oldVal, newVal) {
    //     if (!oldVal && newVal) {
    //         this.inputs.push({oldVal: null, newVal: null});
    //         return;
    //     }
    //     if (newVal === '') {
    //         if (this.inputs.length > 1) {
    //             this.inputs.pop();
    //             return;
    //         }
    //     }
    // }
}



