import {Component, ViewChild} from '@angular/core';
import {SlasherDataService} from '../../services/slasher-data.service';
import {Router} from '@angular/router';

@Component({
    selector: 'slashers-profile-take-picture',
    templateUrl: './profile-take-picture.component.html',
    styleUrls: ['./profile-take-picture.component.scss'],
})
export class ProfileTakePictureComponent {

  @ViewChild('inputFile') inputFile: any;
  showCamera = false;

  constructor(public slasherDataService: SlasherDataService, private router: Router){}

  openCamera() {
    this.showCamera = true;
  }

  openFile() {
    this.inputFile.nativeElement.click();
  }

  onSetImageData(data: string) {
    this.slasherDataService.slasherData.imageSrc = data;
  }

  imagePicked(fileInput: any) {
    let reader = new FileReader();
    reader.onload = (e: any) => {
        this.slasherDataService.slasherData.imageSrc = e.target.result;
        this.router.navigate(['/profile-take-avatar']);
    }
    reader.readAsDataURL(fileInput.target.files[0]);
  }

  validate() {
    if (this.slasherDataService.slasherData.imageSrc) {
      this.slasherDataService.update();
      this.router.navigate(['/profile-take-avatar']);
    }
  }

}



