import {Component, OnInit, Input, ViewChild, ElementRef, HostListener, OnChanges, SimpleChanges} from '@angular/core';

@Component({
    selector: 'slashers-full-cropped-image',
    templateUrl: './full-cropped-image.component.html',
    styleUrls: ['./full-cropped-image.component.scss']
})
export class FullCroppedImageComponent implements OnInit, OnChanges {

  @ViewChild('canvas') canvas: ElementRef;
  @ViewChild('container') container: ElementRef;

  @Input() imageSrc: string;
  @Input()
  cropperPosition = {
    tlPosition: {x:0, y:0},
    brPosition: {x:1, y:1}
  };

  image: any;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.drawImage();
  }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.imageSrc && changes.imageSrc.currentValue) {
      this.image = new Image();
      this.image.src = this.imageSrc;
      this.image.addEventListener('load', () => {
        this.drawImage();
      });
    }
  }


  drawImage() {
    let imageWidth = this.image.width;
    let imageHeight = this.image.height;

    let screenWidth = this.container.nativeElement.offsetWidth;
    let screenHeight = this.container.nativeElement.offsetHeight;

    let cropTopLeftX = this.cropperPosition.tlPosition.x * imageWidth;
    let cropTopLeftY = this.cropperPosition.tlPosition.y * imageHeight;
    let cropBottomRightX = this.cropperPosition.brPosition.x * imageWidth;
    let cropBottomRightY = this.cropperPosition.brPosition.y * imageHeight;

    let dispoWidth = (cropBottomRightX - cropTopLeftX) + 2 * Math.min(cropTopLeftX, imageWidth - cropBottomRightX);
    let dispoHeight = (cropBottomRightY - cropTopLeftY) + 2 * Math.min(cropTopLeftY, imageHeight - cropBottomRightY);

    let scaleX = screenWidth / dispoWidth;
    let scaleY = screenHeight / dispoHeight;

    let scale = Math.max(scaleX, scaleY);

    const context = this.canvas.nativeElement.getContext('2d');
    this.canvas.nativeElement.width = screenWidth;
    this.canvas.nativeElement.height = screenHeight;

    let posCenterX = (cropTopLeftX + (cropBottomRightX - cropTopLeftX) / 2) * scale;

    let posX = posCenterX - (this.canvas.nativeElement.width / 2);

    let posCenterY = (cropTopLeftY + (cropBottomRightY - cropTopLeftY) / 2) * scale;
    let posY = posCenterY - (this.canvas.nativeElement.height / 2);


    context.drawImage(this.image, -posX, -posY, this.image.width * scale, this.image.height * scale);
  }

}



