import {Routes} from '@angular/router';
import {HomeComponent} from './home/home.component';
import {NotFoundComponent} from './not-found/not-found.component';
import {InscriptionComponent} from './inscription/inscription.component';
import {ConnexionComponent} from './connexion/connexion.component';
import {PresentationComponent} from './presentation/presentation.component';
import {WelcomeComponent} from './welcome/welcome.component';
import {Welcome1Component} from './welcome1/welcome1.component';
import {CameraComponent} from './camera/camera.component';
import {ProfileComponent} from './profiles/profile/profile.component';
import {ProfileViewComponent} from './profiles/profile-view/profile-view.component';
import {ProfileUpdateComponent} from './profiles/profile-update/profile-update.component';
import {TakePictureComponent} from './take-picture/take-picture.component';
import {TakeAvatarComponent} from './take-avatar/take-avatar.component';

//bookmark
import {StoryhomeComponent} from './story/home/home.component';
import {Story1Component} from './story/1/1.component';
import {CounterComponent} from './counter/counter.component';
//bookmark

import {ProfileEditLinksComponent} from './profiles/profile-edit-links/profile-edit-links.component';
import {ProfileTakePictureComponent} from './profiles/profile-take-picture/profile-take-picture.component';
import {ProfileTakeAvatarComponent} from './profiles/profile-take-avatar/profile-take-avatar.component';


export const ROUTES: Routes = [
    {path: '', component: HomeComponent},
    {path: 'home', component: HomeComponent},
    {path: 'inscription', component: InscriptionComponent},
    {path: 'connexion', component: ConnexionComponent},
    {path: 'presentation', component: PresentationComponent},
    {path: 'welcome', component: WelcomeComponent},
    {path: 'welcome1', component: Welcome1Component},
    {path: 'camera', component: CameraComponent},
    {path: 'profile', component: ProfileComponent},
    {path: 'profile-view', component: ProfileViewComponent},
    {path: 'profile-update', component: ProfileUpdateComponent},
    {path: 'take-picture', component: TakePictureComponent},
    {path: 'take-avatar', component: TakeAvatarComponent},

    {path: 'story', component: StoryhomeComponent},
    {path: 'story1', component: Story1Component},
    {path: 'counter', component: CounterComponent},


    {path: 'profile-edit-links', component: ProfileEditLinksComponent},
    {path: 'profile-take-picture', component: ProfileTakePictureComponent},
    {path: 'profile-take-avatar', component: ProfileTakeAvatarComponent},
    {path: '**', component: NotFoundComponent}
];



