import {Component, ViewChild} from '@angular/core';
import {SlasherDataService} from '../services/slasher-data.service';
import {Router} from '@angular/router';

@Component({
    selector: 'slashers-take-picture',
    templateUrl: './take-picture.component.html',
    styleUrls: ['./take-picture.component.scss']
})
export class TakePictureComponent {

  @ViewChild('inputFile') inputFile: any;
  showCamera = false;

  @ViewChild('canvasRendering') canvasRendering: any;

  constructor(public slasherDataService: SlasherDataService, private router: Router){}

  openCamera() {
    this.showCamera = true;
  }

  openFile() {
    this.inputFile.nativeElement.click();
  }

  onSetImageData(data: string) {
    this.slasherDataService.slasherData.imageSrc = data;
  }

  imagePicked(fileInput: any) {
    let reader = new FileReader();
    reader.onload = (e: any) => {
        // this.slasherDataService.slasherData.imageSrc = e.target.result;
        // this.router.navigate(['/take-avatar']);

        let image:any = new Image();
        image.src = e.target.result;
        image.addEventListener('load', () => {
          let context = this.canvasRendering.nativeElement.getContext('2d');
          this.canvasRendering.nativeElement.width = image.width;
          this.canvasRendering.nativeElement.height = image.height;
          context.drawImage(image, 0, 0, this.canvasRendering.nativeElement.width, this.canvasRendering.nativeElement.height);

          let imageUrl = this.canvasRendering.nativeElement.toDataURL();
          this.slasherDataService.slasherData.imageSrc = imageUrl;
          this.router.navigate(['/take-avatar']);
        });

    }
    reader.readAsDataURL(fileInput.target.files[0]);
  }

  goNext() {
    this.slasherDataService.update();
    if (!this.slasherDataService.slasherData.imageSrc) {
      this.router.navigate(['/welcome']);
    } else {
      this.router.navigate(['/take-avatar']);
    }
  }



}



