import {Component, Output, EventEmitter, OnInit} from '@angular/core';
import { Ng2DeviceService } from 'ng2-device-detector';

@Component({
    selector: 'slashers-choose-picture',
    templateUrl: './choose-picture.component.html',
    styleUrls: ['./choose-picture.component.scss']
})
export class ChoosePictureComponent implements OnInit {

    @Output() openCamera: EventEmitter<any> = new EventEmitter<any>();
    @Output() openFile: EventEmitter<any> = new EventEmitter<any>();

    isPhoneTablet: boolean;

    constructor(private deviceService: Ng2DeviceService) {}

    ngOnInit() {
      let deviceInfo = this.deviceService.getDeviceInfo();
      if (deviceInfo.device && deviceInfo.device != 'unknown') {
        this.isPhoneTablet = true;
      }
    }

    clickToOpenCamera() {
      this.openCamera.emit();
    }

    clickToOpenFile() {
      this.openFile.emit()
    }
}



